'use strict';

/**
* Natural logarithm of the probability mass function (PDF) for a degenerate distribution.
*
* @module @stdlib/stats-base-dists-degenerate-logpmf
*
* @example
* import logpmf from '@stdlib/stats-base-dists-degenerate-logpmf';
*
* var y = logpmf( 2.0, 0.0 );
* // returns -Infinity
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-degenerate-logpmf';
*
* var logpmf = factory( 10.0 );
*
* var y = logpmf( 10.0 );
* // returns 0.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
